.trip {
    text-align: center;
    margin: 4rem 6rem;
    color: #2a2a2a;
}
.trip h1{
    font-size: 3rem;
}

.tripcard{
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
}
.t-card{
    width: 32%;
    text-align: justify;
    box-shadow: 0 5px 25px 2px rgba(0, 0, 0, 0.11);
    border-radius: 7px;
    cursor: pointer;
    padding: 1rem .8rem;
}

.t-image {
    height: 200px;
    /* width: 100px; */
    overflow: hidden;
    border-radius: 7px;

}
.t-image img{
    height: 100%;
    width: 100%;
    transition: 0.3s ease-in-out;
    border-radius: 7px;
}
.t-image:hover img{
transform: scale(1.3);
}
.t-card h4{
    font-size: 1.3rem;
    /* padding: 9rem 0 2rem 0; */
}
@media screen and (max-width : 850px){
    .trip h1{
        margin-top: -110px;
       font-size: 15px;
     }
    .trip p {
        font-size: 10px;
        margin-bottom: -35px;
    
    }


    .tripcard{
        margin-top: 3rem;
        display: flex;
        justify-content:center;
        align-items: center;
    }
    .t-card{
        width: 100%;
        font-size: 10px;
        text-align: start;
        box-shadow: 0 5px 25px 2px rgba(0, 0, 0, 0.11);
        border-radius: 7px;
        cursor: pointer;
        padding: 1rem .8rem;
        padding-bottom: 50px;
    }
    
    .t-image {
        height: 100px;
        width: 90px;
        overflow: hidden;
        border-radius: 7px;
    
    }
    .t-image img{
        height: 100%;
        width: 100%;
        transition: 0.3s ease-in-out;
        border-radius: 7px;
    }
    .t-image:hover img{
    transform: scale(1.3);
    }
    .t-card h4{
        font-size: 10px;
        /* padding: 9rem 0 2rem 0; */
    }





}